.warningContainer {
    all: unset;
    position: fixed;
    top: 0;
    right: 0;
    margin: 8px 8px 0 0;
    padding: 16px 24px 16px 20px;
    background-color: darkred;
    color: white;
}

.closeButton {
    position: absolute;
    top: -2px;
    right: 6px;
    font-size: larger;
}
